.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 30px;
  margin-bottom: 80px;
  padding: 25px 20px;
}

.header__title {
  margin: 0;
  margin-right: 6.6%;
  font-size: 50px;
}

.header__list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header__list-item {
  margin-right: 2.5%;
}

.header__link {
  color: var(--green);
  text-decoration: none;
}

.header__link:hover {
  text-decoration: underline;
}
.random-planet {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 80px;
  padding: 20px;
  min-height: calc(20px + 250px + 2px); /* padding image border */
}

.random-planet__img {
  width: 250px;
  height: 250px;
  border-radius: 5px;
  margin-right: 5%;
}

.random-planet__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.random-planet__list-item {
  padding: 10px;
  border-top: 1px solid var(--border-color);
}

@keyframes lds-rolling {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@-webkit-keyframes lds-rolling {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.spinner {
  -webkit-align-self: center;
          align-self: center;
  margin: 0 auto;
}

.lds-rolling {
  position: relative;
}
.lds-rolling div,
.lds-rolling div:after {
  position: absolute;
  width: 160px;
  height: 160px;
  border: 20px solid #00bc8c;
  border-top-color: transparent;
  border-radius: 50%;
}
.lds-rolling div {
  -webkit-animation: lds-rolling 1s linear infinite;
  animation: lds-rolling 1s linear infinite;
  top: 100px;
  left: 100px;
}
.lds-rolling div:after {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.lds-rolling {
  width: 100px;
  height: 100px;
  -webkit-transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
  transform: translate(-50px, -50px) scale(0.5) translate(50px, 50px);
}
.row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 80px;
}

.row__left {
  width: 48%;
}

.row__right {
  width: 48%;
}

:root {
  --dark-gray: #222222;
  --gray: #444444;
  --green: #00bc8c;
  --light-gray: #303030;
  --border-color: var(--gray);
}

body {
  margin: 0;
  font-size: 18px;
  color: #ffffff;
  background-color: #222222;
  background-color: var(--dark-gray);
}

.box {
  border: 1px solid #444444;
  border: 1px solid var(--border-color);
  background-color: #303030;
  background-color: var(--light-gray);
  border-radius: 5px;
}

@media  (min-width: 1200px) {
  .container {
    width: 1550px;
    margin: 0 auto;
  }
}
